import { Component, OnInit } from '@angular/core';
import {GeneralService} from '../services/general.service';
import { ActivatedRoute } from '@angular/router';
import {objCatalogos} from '../Interfaces/Interfaces' 
import {Router} from '@angular/router'

@Component({
  selector: 'app-doc-selector',
  templateUrl: './doc-selector.component.html',
  styleUrls: ['./doc-selector.component.css']
})
export class DocSelectorComponent implements OnInit {
  public boton = "Continuar";
  public datos =  new Array(4);
  public objCatalog = new objCatalogos;
  public descripcionSeleccionada : string;
  public valorSeleccionado : number;
  public jsonVariable = [
    {
      titulo : "¿Qué comprobante vas a subir?",
      datos  : ["Recibo Nóminas","Estado de cuenta","Recibos de pago"]
    },
    {
      titulo : "¿Qué identificación vas a subir?",
      datos  : ["INE","Pasaporte","Fm2 o Fm3"]
    }
  ]

  public Titulo  = this.jsonVariable[0]['titulo'];
  

  constructor(public responseService : GeneralService,public activatedRoute: ActivatedRoute,private route: Router) { }

  ngOnInit(): void {

    let iddocto  = this.activatedRoute.snapshot.params.iddocto; 
    this.responseService.obtenerCatalogo(iddocto).subscribe(response => {
     
      this.objCatalog = response;
      console.log(this.objCatalog)
    })
  }


  seleccionarBoton(e,valor : number,descripcion : string){
    this.descripcionSeleccionada = descripcion;
    this.valorSeleccionado = valor;
    let element = <HTMLInputElement>e.originalTarget;
    element.style.backgroundColor = "blue";
    
    let elementos =  (<HTMLCollection>document.getElementsByClassName("btnSelector"));
    for(let i = 0; i < elementos.length; i++){

      if(element != elementos[i]){
        (<HTMLInputElement>elementos[i]).style.backgroundColor = "white";
      }
    }
  }

  enviarDocumento(){
    let folio  = this.activatedRoute.snapshot.params.folio; 
    let iddocto  = this.activatedRoute.snapshot.params.iddocto; 
    let texto = this.descripcionSeleccionada;

  }

}
