import { Component, OnInit } from '@angular/core';
import {GeneralService} from '../services/general.service';
import {WelcomeMessage} from '../Interfaces/Interfaces'
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-start-message',
  templateUrl: './start-message.component.html',
  styleUrls: ['./start-message.component.css']
})
export class StartMessageComponent implements OnInit {
  public welcome : WelcomeMessage;
  public message : string;
  public name : string;
  public folio : string;
  constructor(public responseService : GeneralService,private activatedRoute: ActivatedRoute) { 
    
    this.folio = this.activatedRoute.snapshot.params.folio;
    
    responseService.getWelcomeMessage(this.folio).subscribe(response => {
      this.welcome = response;
      this.message = this.welcome.message;
      
    })

  }

  ngOnInit(): void {}

  
   

}
