import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {Welcome2Component} from './welcome2/welcome2.component'
import {Welcome1Component} from './welcome1/welcome1.component'
import {TcComponent} from './tc/tc.component'
import {InfoSolicitanteComponent} from './info-solicitante/info-solicitante.component'
import {InfoDomicilioComponent} from './info-domicilio/info-domicilio.component'
import {DocSelectorComponent} from './doc-selector/doc-selector.component'
import {DocSelector4Component} from './doc-selector4/doc-selector4.component'
import {DocSelector5Component} from './doc-selector5/doc-selector5.component'
import {PerfilComponent} from './perfil/perfil.component'
import {ConfirmButtonComponent} from './confirm-button/confirm-button.component'
import {OptionsComponent} from './options/options.component'
import {ContratoArrendamientoComponent} from './contrato-arrendamiento/contrato-arrendamiento.component'
import {SliderComponent} from './slider/slider.component'
import {DomicilioFiscalComponent} from './domicilio-fiscal/domicilio-fiscal.component'
import {SuccessComponent} from './success/success.component'
import {FailUploadComponent} from './fail-upload/fail-upload.component'
import {SeccionAyudaComponent} from './seccion-ayuda/seccion-ayuda.component'
import {MainPendingComponent} from './main-pending/main-pending.component'
import {LoaderComponent} from './loader/loader.component'
import {InfoDomicilio2Component} from './info-domicilio2/info-domicilio2.component'
import {HSliderComponent} from './h-slider/h-slider.component'
import {FaqComponent} from './faq/faq.component'
import { StartMessageComponent } from './start-message/start-message.component';



const routes: Routes = [
  { path: '', redirectTo:"doc-selector" , pathMatch: 'full'},
  { path: 'welcome2/:folio', component: Welcome2Component },
  { path: 'welcome1/:tipo/:folio', component: Welcome1Component },
  { path: 'tc', component: TcComponent },
  { path: 'info-solicitante', component: InfoSolicitanteComponent },
  { path: 'info-domicilio', component: InfoDomicilioComponent },
  { path: 'doc-selector4', component: DocSelector4Component },
  { path: 'doc-selector5', component: DocSelector5Component },
  { path: 'perfil', component: PerfilComponent },
  { path: 'confirm-button', component: ConfirmButtonComponent },
  { path: 'options', component: OptionsComponent },
  { path: 'contrato-arrendamiento', component: ContratoArrendamientoComponent },
  { path: 'slider', component: SliderComponent },
  { path: 'domicilio-fiscal', component: DomicilioFiscalComponent },
  { path: 'success', component: SuccessComponent }, 
  { path: 'fail-upload', component: FailUploadComponent },
  { path: 'seccion-ayuda', component: SeccionAyudaComponent },
  { path: 'main-pending/:tipo/:folio/:number', component: MainPendingComponent },
  { path: 'loader', component: LoaderComponent },
  { path: 'info-domicilio2', component: InfoDomicilio2Component },
  { path: 'h-slider', component: HSliderComponent },
  { path: 'doc-selector/:folio/:tipo/:digitos/:iddocto/:iddoctodep/:valordep', component: DocSelectorComponent },
  { path: 'faq', component: FaqComponent },
  { path: 'startMessage/:folio', component: StartMessageComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
