import { Component, OnInit } from '@angular/core';
import * as $ from "jquery";

@Component({
  selector: 'app-info-domicilio',
  templateUrl: './info-domicilio.component.html',
  styleUrls: ['./info-domicilio.component.css']
})
export class InfoDomicilioComponent implements OnInit {
  public boton = "Continuar";
  constructor() { }

  ngOnInit(): void {
    $('#inputEstado').data('open',false);
    $('#inputEstado').click( function() {
        if ( $('#inputEstado').data('open') == false ) {
            var sel = document.getElementById("inputEstado");
            $('#inputEstado').data('open', true);
            sel.classList.remove("colorNormal");
            sel.classList.add("colorBlanco");
        } else {
            $('#inputEstado').data('open', false );
        }
    });
  }

  selectData(value:string){
    var sel = document.getElementById("inputEstado");
    if(value == ""){
      sel.classList.add("colorBlanco");
      sel.classList.remove("colorNormal");
    }else{
      sel.classList.remove("colorBlanco");
      sel.classList.add("colorNormal");
    }
  }

  // ajusteSelect(value:string){
  //   console.log(value);
  //   var sel = document.getElementById("inputEstado");
  //   if(value == ""){
  //     sel.classList.remove("colorNormal");
  //     sel.classList.add("colorBlanco");
  //   }
    
  // }

}


