import { Injectable } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { HttpClient,HttpHeaders} from '@angular/common/http'
import {PantallaBienvenida, RespuestaGeneral, WelcomeMessage,objIniciaProcesoOnboarding,objCatalogos} from '../Interfaces/Interfaces'
import {peticionDatosLogin, objDigitosValidacion} from '../Interfaces/Interfaces-Out'
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})


export class GeneralService {

 private url = "http://localhost:60581" // DEV
  //private url = http://3.135.198.99:8088""  //PROD
  
  constructor(private client : HttpClient) { }

  
  getResponse() : Observable<RespuestaGeneral>{
    return this.client.get<RespuestaGeneral>("/Service1.svc/InicioPortal/CM210706007538");
  }

  getWelcomeMessage(folio : string) : Observable<WelcomeMessage>{
    return this.client.get<WelcomeMessage>("/Service1.svc/InicioPortal/"+folio)
  }

  getPantallaBienvenida(folio : string) : Observable<PantallaBienvenida>{
    return this.client.get<PantallaBienvenida>("/Service1.svc/PantallaBienvenida/"+folio)
  }

  setDatosUsuarioLogin(datosPeticion : peticionDatosLogin ) : Observable<RespuestaGeneral>{
    let headers=new HttpHeaders();
    headers.append('Content-Type' , 'application/json');
    var json = JSON.stringify(datosPeticion);
    var jsonData = "{\"jsonData\":"+json+"}";
    const body= datosPeticion;
    
    
    return this.client.post<RespuestaGeneral>("/Service1.svc/SetCredencialesLogin",body,{headers : headers})
  }

  validateNumbers(objet : objDigitosValidacion) : Observable<objIniciaProcesoOnboarding>{
    let headers=new HttpHeaders();
    headers.append('Content-Type' , 'application/json');
    const body= objet;
    return this.client.post<objIniciaProcesoOnboarding>("/Service1.svc/ValidacionCelular",body,{headers : headers})
  }

  obtenerCatalogo(idDocto : string) : Observable<objCatalogos>{
      return this.client.get<objCatalogos>("/Service1.svc/ObtieneValoresDocto/"+idDocto,)
  }
  
}
