import { Component, OnInit, Output } from '@angular/core';
import {GeneralService} from '../services/general.service';
import { ActivatedRoute } from '@angular/router';
import {objDigitosValidacion} from '../Interfaces/Interfaces-Out'
import {} from '../Interfaces/Interfaces' 
import {Router} from '@angular/router'
@Component({
  selector: 'app-welcome1',
  templateUrl: './welcome1.component.html',
  styleUrls: ['./welcome1.component.css']
})
export class Welcome1Component implements OnInit {

  @Output () responseObj :objDigitosValidacion 
  public boton = "Confirmar";
  public tipo = 0;
  public folio = "";

  constructor(public responseService : GeneralService,public activatedRoute: ActivatedRoute,private route: Router) { 

   
    responseService.getResponse().subscribe(response => {
      console.log(response)
    })

  }

  ngOnInit(): void {
    this.tipo = this.activatedRoute.snapshot.params.tipo;    
    this.folio = this.activatedRoute.snapshot.params.folio;
  }

example(element,number,e){
  
  let keyCode = e.keyCode;
  var numbers = /^[0-9]+$/;
      

  switch(number){

    case 1: 
     if(keyCode != 8){
      if(!(<HTMLInputElement>document.getElementById("number-1")).value.match(numbers)){
        (<HTMLInputElement>document.getElementById("number-1")).value = "";
        
        alert("Solo puedes ingresar numeros")
        return false;
      }
      (<HTMLInputElement>document.getElementById("number-2")).focus();
     }
    break;
    case 2: 
    if(keyCode != 8){
      if(!(<HTMLInputElement>document.getElementById("number-2")).value.match(numbers)){
        (<HTMLInputElement>document.getElementById("number-2")).value = "";
        
        alert("Solo puedes ingresar numeros")
        return false;
      }
      (<HTMLInputElement>document.getElementById("number-3")).focus();
     }else{
      (<HTMLInputElement>document.getElementById("number-1")).focus();
     }
    break;
    case 3: 
    if(keyCode != 8){
      if(!(<HTMLInputElement>document.getElementById("number-3")).value.match(numbers)){
        (<HTMLInputElement>document.getElementById("number-3")).value = "";
        
        alert("Solo puedes ingresar numeros")
        return false;
      }
      (<HTMLInputElement>document.getElementById("number-4")).focus();
     }else{
      (<HTMLInputElement>document.getElementById("number-2")).focus();
     }
    break;
    case 4: 
    if(keyCode == 8){
      if(!(<HTMLInputElement>document.getElementById("number-4")).value.match(numbers)){
        (<HTMLInputElement>document.getElementById("number-4")).value = "";
        
        alert("Solo puedes ingresar numeros")
        return false;
      }
      (<HTMLInputElement>document.getElementById("number-3")).focus();
     }
    break;

  }  
}

sendNumber(){
  let strgBuilder = "";

  for(let i = 1; i < 5; i++){

    let element = (<HTMLInputElement>document.getElementById("number-"+i));
    if(element.value == ""){
      alert("Todos los campos son obligatorios");
      return false;
        }
        strgBuilder += element.value;    

  }
  
  let requestObject = new objDigitosValidacion;
  requestObject.idFolio = this.folio;

  requestObject.digitos = strgBuilder;
  requestObject.tipo = this.tipo;

  this.route.navigate(["/main-pending/"+this.tipo+"/"+this.folio+"/"+strgBuilder])
 
}
}
